console.log(lang)
/*
zh: chinese
en: english 
nl: dutch
fr: french
de: german
ja: japanese
it: itanin
pt: portuguese
es: spanish
sw: swedish
*/
// window.lang = 'en'
window.lang = lang.substr(0, 2)

export function localStr(str) {
	let table = languages[window.lang]
	if (!table) {
		table = languages.en
	}
	if (table) {
		let res = table[str]
		if (res) {
			return res
		}
	}
	return str
}



