const normalTerms = [dynamics, dynamicChange, tempoMarks, tempoChange, loopMarks, playTech, musicalSignSymbol]

class QMusicTerm extends QScore {
	constructor() {
		super()
		this.english = ''
		this.meaning = ''
		this.choiceNames = []
		this.choiceIndexs = []
		this.maxChoiceLen = 0
	}
	getChoices(items, itemIndex) {
		let item = items[itemIndex]
		this.meaning = window.lang==='zh' ? item.chinese : item.english
		let start = 0 //itemsIndex>5 ? itemsIndex-5 : 0
		let end = items.length //start+10
		if (items.length>10) {
			if (itemIndex>5) {
				start = itemIndex-QNoteElem.random(5)
			}
			// start = itemIndex>5 ? itemIndex-5 : 0
			if (start+10<end) {
				end = start+10
			}
		}
		for (let i = start; i < end; i++) {
			let item = items[i]
			let curMeaning = window.lang==='zh' ? item.chinese : item.english
			if (!this.choiceNames.includes(curMeaning)) {
				this.choiceNames.push(curMeaning)
				if (!curMeaning) {
					console.log('error music term')
					console.log(item)
				}
				if (curMeaning.length>this.maxChoiceLen) {
					this.maxChoiceLen = curMeaning.length
				}
			}
		}
		this.choiceIndexs = [this.choiceNames.indexOf(this.meaning)]

		if (item.short) {
			if (item.short instanceof Array) {
				this.english = item.short[0]
			}else{
				this.english = item.short
			}
		}else if (item.italian) {
			this.english = item.italian
		}else if (item.french) {
			this.english = item.french
		}else if (item.german) {
			this.english = item.german
		}else {
			this.english = item.english
		}
	}

	/*
	config: null为随机和弦
	{
		level: 1...5
		terms: ['Common terms','Other terms']
	}
	*/
	static randomMusicTerm(config) { 
		let res = new QMusicTerm()
		res.randomNormalTerm(config.terms, config.level)
		return res
	}
	randomNormalTerm(terms, level) {
		let type = QNoteElem.randomItem(terms)
		let items
		console.log('level='+level)
		if (type === 'Other terms' && (!level || level>1)) {
			items = otherTerms
		}else {
			items = QNoteElem.randomItem(normalTerms)
		}
		if (level) {
			items = items.filter((item) => {
				return item.level && item.level<=level
			})
		}
		let itemIndex = QNoteElem.random(items.length-1)
		this.getChoices(items, itemIndex)
	}
}

export default QMusicTerm;



