import QMusicTerm from './QMusicTerm';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';
import {localStr} from '../config/language';

class QMusicTermQuestions extends BaseQuestions {
	/*config={
		level: 1...5
		terms: ['Common terms','Other terms']
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.genQuestionIdentification(questions, config)
		}
		
		return questions
	}
	static genQuestionIdentification(questions, config) {
		let term = QMusicTerm.randomMusicTerm(config)
		var question = new Question()
		question.desc = localStr('Choose correct meanings of the term: ')+term.english
		question.choices = term.choiceNames
		question.answers = term.choiceIndexs
		question.keyboard = term.maxChoiceLen>2 ? KeyboardType.ChoiceABCD : KeyboardType.Choice
		questions.push(question)
	}
	
}
export default QMusicTermQuestions;



