import QNoteElem from './QNote';
import QHarmony from './QHarmony';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';
import {localStr} from '../config/language';

class QHarmonyQuestions extends BaseQuestions {
	static genQuestions(config) {
		let questions = []
		if (!config.qtypes || config.qtypes.includes('Identification')) {
			this.questionIdentification(questions, config)
		}

		return questions
	}
	static questionIdentification(questions, config) {
		let harmony = QHarmony.randomHarmonyStaff(config)
		let events = this.eventsOfNotes(harmony.score.notes)

		var question = new Question()
		question.score = harmony.score
		if (window.device === 'piano') {
			question.midi = {countdown: false, beats: harmony.score.beats, beatUnit: harmony.score.beatUnit, tempo: 120, events:events}
		}
		question.keyboard = KeyboardType.Choice

		let questionType = QNoteElem.random(1)
		if (questionType) { 
			let measure = QNoteElem.random(harmony.progress.length-2, 1)
			let curChordNames = harmony.chordNames(measure)
			question.desc = `识别第${measure+1}小节和弦功能：`+harmony.progressDescription()
			question.choices = config.chords
			question.answers = curChordNames.map((item)=>{return config.chords.indexOf(item)})
		}else{
			question.desc = `判断终止式：`+harmony.progressDescription()
			question.choices = ['正格终止', '变格终止', '半终止', '阻碍终止']
			question.answers = [question.choices.indexOf(harmony.stopName)]
		}

		questions.push(question)
	}
}

export default QHarmonyQuestions;



