import QRhythm from './QRhythm';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';
import {localStr} from '../config/language';

class QRhythmQuestions extends BaseQuestions {
	/*config={
		level: 1...5
		clefs: ['Treble', 'Bass']
		beats: ['2/4拍','3/4拍','4/4拍','3/8拍','6/8拍']
		measures: ['2','4','6','8']
		notes: ['C','#C','D','♭E','E','F','#F','G','♭A','A','♭B','B']
		rhythmTypes: ['1', '2.', ...]
	}
	*/
	static genQuestions(config) {
		let questions = []
		config.isMelody = (config.notes.length>1) //有多个音高的是旋律
		config.clef = this.randomClef(config.clefs)
		if (config.clef === 'Grand Staff') {
			config.clef = "treble"
		}

		if (!config.qtypes ||  config.qtypes.includes('Keyboard')) {
			this.genQuestionKeyboard(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('EarTraining')) {
			this.genQuestionEarTraining(questions, config)
		}
		
		return questions
	}
	static genQuestionKeyboard(questions, config) {
		let rhythm = QRhythm.randomRhythm(config)
		let events = this.eventsOfNotes(rhythm.notes, rhythm.beats, rhythm.beatUnit)
		var question = new Question()
		question.desc = config.isMelody ? localStr('Play melody') : localStr('Play rhythm')
		question.score = {clef: config.clef, fifths: 0, beats: rhythm.beats, beatUnit: rhythm.beatUnit, notes: rhythm.notes}
		question.midi = {countdown: true, beats: rhythm.beats, beatUnit: rhythm.beatUnit, events:events}
		question.answers = {notes: rhythm.notes}
		question.keyboard = KeyboardType.PlayRhythm
		questions.push(question)
	}
	static genQuestionEarTraining(questions, config) {

		let rhythm = QRhythm.randomRhythm(config)
		let events = this.eventsOfNotes(rhythm.notes, rhythm.beats, rhythm.beatUnit)
		var question = new Question()
		question.desc = config.isMelody ? localStr('Listen and write the melody') : localStr('Listen and write the rhythm')
		question.score = {clef: config.clef, fifths: 0, beats: rhythm.beats, beatUnit: rhythm.beatUnit} //, notes: rhythm.notes}
		question.midi = {countdown: true, beats: rhythm.beats, beatUnit: rhythm.beatUnit, events:events}
		question.answers = {notes: rhythm.notes}
		question.keyboard = KeyboardType.WriteRhythm
		questions.push(question)
	}
	static onRhythmKey(question, tempo, key, down, timestamp) {
		if (!down) {
			return
		}
		let firstEvent = question.midi.events[0]
		// console.log(firstEvent)
		if (firstEvent.tapTick === null) {
			if (firstEvent.note===key) {
				//开始评分
				this.startTimestamp = timestamp
				console.log('startTimestamp:'+timestamp)
			}else{
				console.log('note start:'+firstEvent.note)
				return
			}
		}
		let secPerTick = 60.0/tempo/480.0;
		let curTick = Math.floor((timestamp-this.startTimestamp)/secPerTick)
		// console.log('timestamp:'+timestamp)
		// console.log('curTick:'+curTick)
		let delta = 34 //48: 0.1s, 34: 0.071s, 24: 0.05s, 20: 0.042s
		let marks = 0
		let finished = false
		let notes = question.score.notes.filter((note)=>{
			return !note.rest
		})
		for (let i = 0; i < question.midi.events.length; i++) {
			let event = question.midi.events[i]
			if (event.mark) {
				marks += event.mark
			}
			if(event.tapTick===null) {
				event.tapTick = curTick
				let flag = {color: 'red'}
				if (event.tapTick>event.tick+delta*4) {
					flag.text = '⇏'
				}else if (event.tapTick>event.tick+delta*2) {
					flag.text = '⇉'
					flag.color = '#e975ec'
					event.mark = 1
				}else if (event.tapTick>event.tick+delta) {
					flag.text = '→'
					flag.color = '#f0a700'
					event.mark = 2
				}else if (event.tapTick<event.tick-delta*4) {
					flag.text = '⇍'
				}else if (event.tapTick<event.tick-delta*2) {
					flag.text = '⇇'
					flag.color = '#e975ec'
					event.mark = 1
				}else if (event.tapTick<event.tick-delta) {
					flag.text = '←'
					flag.color = '#f0a700'
					event.mark = 2
				}else{
					flag.text = '='
					flag.color = '#009E3D'
					event.mark = 3
				}
				notes[i].flags.push(flag)
				if (event.note===key) {
					notes[i].flags.push({text: '√', color: '#009E3D', placement: 'below'})
				}else{
					event.mark = 0
					notes[i].flags.push({text: 'x', color: 'red', placement: 'below'})
				}
				console.log(`${i}:${curTick} ${flag} ${event.tick}`)

				marks += event.mark
				question.mark = Math.floor(100*marks/((i+1)*3))
				if (i===question.midi.events.length-1) {
					finished = true
					if (question.mark>=80) {
						question.correct = true
					}else{
						question.correct = false
					}
				}
				break
			}
		}
		return {marks: marks, finished: finished}
	}
}
export default QRhythmQuestions;



