import QNoteElem from './QNote';
import QScale from './QScale';
import BaseQuestions, {Question, KeyboardType} from './BaseQuestions';
import {localStr} from '../config/language';

const sameScale = [
	['自然大调', '伊奥尼亚', '清乐宫调式', '雅乐徵调式'],
	['自然小调', '爱奥尼亚', '清乐羽调式', '雅乐角调式', '燕乐商调式'],
	['多利亚',   '清乐商调式', '雅乐羽调式'],
	['弗里几亚', '清乐角调式', '燕乐羽调式'],
	['利低亚',   '雅乐宫调式', '燕乐徴调式'],
	['混合利底亚', '清乐徵调式', '燕乐宫调式', '雅乐商调式'],
	['洛克里亚', '燕乐角调式'],
]
class QScaleQuestions extends BaseQuestions {
	/*config={
		level: 1...5
		clefs: [Trable, Bass, C-clefs]
		keySignatures: [-7 ... 7]
		scales: ['自然大调', '自然小调','和声小调','旋律小调']
		qtypes: [Identification, Construction, Keyboard]
	}
	*/
	static genQuestions(config) {
		config.clef = this.randomClef(config.clefs)
		if (!config.scaleNames) {
			config.scaleNames = []
			/*
			如果包含：
			['五声调式','清乐七声调式','雅乐七声调式','燕乐七声调式']
			再随机生成具体哪种民族调式：
			['五声宫调式','五声商调式','五声角调式','五声徵调式','五声羽调式']	
			['清乐宫调式','清乐商调式','清乐角调式','清乐徵调式','清乐羽调式']
			['雅乐宫调式','雅乐商调式','雅乐角调式','雅乐徵调式','雅乐羽调式']
			['燕乐宫调式','燕乐商调式','燕乐角调式','燕乐徵调式','燕乐羽调式']
			*/
			for (let scaleType of config.scales) {
				if (scaleType.endsWith('调式')) {
					if (scaleType.startsWith('五声')) {
						config.scaleNames.push('五声宫调式','五声商调式','五声角调式','五声徵调式','五声羽调式')
					}else if (scaleType.startsWith('清乐')) {
						config.scaleNames.push('清乐宫调式','清乐商调式','清乐角调式','清乐徵调式','清乐羽调式')
					}else if (scaleType.startsWith('雅乐')) {
						config.scaleNames.push('雅乐宫调式','雅乐商调式','雅乐角调式','雅乐徵调式','雅乐羽调式')
					}else if (scaleType.startsWith('燕乐')) {
						config.scaleNames.push('燕乐宫调式','燕乐商调式','燕乐角调式','燕乐徵调式','燕乐羽调式')
					}
				}else {
					config.scaleNames.push(scaleType)
				}
			}
		}


		let questions = []
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			if (QNoteElem.random(1)) {
				this.genQuestionKeyIdentification(questions, config)
			}else{
				this.genQuestionIdentification(questions, config)
			}
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			this.genQuestionConstruction(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Keyboard')) {
			this.genQuestionKeyboard(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('EarTraining')) {
			this.genQuestionEarTraining(questions, config)
		}
		
		return questions
	}
	static answersForChoices(choices, scaleType) {
		for (let items of sameScale) {
			if (items.includes(scaleType)) {
				let answers = []
				for (let item of items) {
					let index = choices.indexOf(item)
					if (index>=0) {
						answers.push(index)
					}
				}
				return answers
			}
		}
		return [choices.indexOf(scaleType)]
	}
	static genQuestionIdentification(questions, config) {
		let scale = QScale.randomScale(config.clef, config.keySignatures, config.scaleNames)
		var choices = config.scaleNames //['自然大调','自然小调','和声小调','旋律小调', ...]
		// let answers = [choices.indexOf(scale.type)]
		let answers = this.answersForChoices(choices, scale.type)
		if (answers[0]<0) {
			console.log('error: to set answer:'+scale.type)
			console.log(choices)
		}

		var question = new Question()
		question.desc = localStr('Choose correct mode')
		question.score = {clef: config.clef, notes: scale.notes}
		question.choices = choices
		question.answers = answers
		question.canChooseOne = true
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
	static genQuestionKeyIdentification(questions, config) {
		let scale = QScale.randomScale(config.clef, config.keySignatures, config.scaleNames)
		let choices = ['C','D','E','F','G','A','B','#','♭']
		let stepCh = scale.name.charAt(scale.name.length-1).toUpperCase()
		let answers = [choices.indexOf(stepCh)]
		if (scale.name.startsWith('#')) {
			answers.push(7)
		}else if (scale.name.startsWith('♭')) {
			answers.push(8)
		}
		if (answers[0]<0) {
			console.log(answers)
		}

		var question = new Question()
		question.desc = localStr('With key signature, this is ( ) ')+localStr(scale.type)
		question.score = {clef: config.clef, fifths: scale.fifths}
		question.choices = choices
		question.answers = answers
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
	static genQuestionConstruction(questions, config) {
		let scale = QScale.randomScale(config.clef, config.keySignatures, config.scaleNames)
		if (!scale.name) {
			console.log('error: randomScale ')
			console.log(scale)
		}
		let notes = [...scale.notes]
		if (scale.downNotes) {
			notes.push(...scale.downNotes)
		}
		var question = new Question()
		if (QNoteElem.random(2)>0) {
			let step = QNoteElem.random(notes.length-1)
			let name = localStr(scale.scaleStepName(step))
			if (name) {
				let note = notes[step].elems[0]
				let pitchName = note.textOfAlter()+note.textOfStep()
				if (window.lang === 'zh') {
					question.desc = '写出'+pitchName+'为'+name+'的'+scale.type+'音阶'
				}else{
					question.desc = 'Write '+localStr(scale.type)+' scale with '+pitchName+' as '+name
				}
				//write xxxx scale with a as subdom...

				// if (scale.type==='旋律小调') {
				// 	question.desc += '(上行)'
				// }
			}
		}
		if (!question.desc) {
			question.desc = localStr('Write scale: ')+scale.fullname
		}
		if (scale.type==='旋律小调') {
			question.desc += ' ('+localStr('ascending and descending')+')'
		}else {
			question.desc += ' ('+localStr('ascending')+')'
		}
		question.score = {clef: config.clef, fifths: 0}
		// question.choices = choices
		question.answers = {ignoreOctave: true, notes: notes}
		question.keyboard = KeyboardType.WriteNotes
		questions.push(question)
	}
	static genQuestionKeyboard(questions, config) {
		let scale = QScale.randomScale(config.clef, config.keySignatures, config.scaleNames)
		if (!scale.name) {
			console.log('error: randomScale ')
			console.log(scale)
		}		
		var question = new Question()
		question.desc = localStr('Play scale: ')+scale.fullname //'弹奏'+scale.fullname+'的音阶'
		if (scale.type==='旋律小调') {
			question.desc += ' ('+localStr('ascending and descending')+')'
		}else {
			question.desc += ' ('+localStr('ascending')+')'
		}
		// question.score = {fifths: 0}
		// question.choices = choices
		question.answers = {ignoreOctave: true, notes: scale.notes}
		question.keyboard = KeyboardType.PlayKeys
		questions.push(question)
	}
	static genQuestionEarTraining(questions, config) {
		
		let scale = QScale.randomScale(config.clef, config.keySignatures, config.scaleNames)
		if (!scale.name) {
			console.log('error: randomScale ')
			console.log(scale)
		}
		let choices = config.scaleNames
		// let answers = [choices.indexOf(scale.type)]
		// if (answers[0]<0) {
		// 	console.log('error: to set answer:'+scale.type)
		// 	console.log(choices)
		// }
		let answers = this.answersForChoices(choices, scale.type)
		if (answers[0]<0) {
			console.log('error: to set answer:'+scale.type)
			console.log(choices)
		}

		let events = []
		let tick = 0
		for (let note of scale.notes) {
			let duration = 240
			events.push({tick: tick, note: note.elems[0].value, duration: duration})
			tick += duration
		}
		let question = new Question()
		question.answers = answers
		question.choices = choices
		question.desc = localStr('Listen scale and choose corrent mode')
		question.midi = {events:events}
		question.canChooseOne = true
		question.keyboard = KeyboardType.Choice
		questions.push(question)
	}
}

export default QScaleQuestions;



