import React, { Component } from 'react';
import Toolbar from '../components/Toolbar';
import MusicSetting from './MusicSetting';
import '../App.css';
import '../Home.css';
import {localStr} from '../config/language';

export class LeftTitle extends Component {
	render() {
		let width =  window.document.body.offsetWidth
		let height = window.device==='piano' ? 1080 : '100%' //window.device==='piano' window.document.body.offsetHeight

		//let height = window.document.body.offsetHeight
		let border_background = 'linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,1) 50%, rgba(200,200,200,0))'
		let background = 'linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,0.5) 50%, rgba(200,200,200,0))'
		let leftDiv = 
			<div style={{display: 'table-cell', width: width/2+10, height: height, verticalAlign: 'middle'}}>
				<div style={{background: border_background, height: 2, margin: '0px 40px'}} />
				<p style={{fontSize:80, textAlign: 'center', background: background, padding: 80, margin: '0px 40px'}}>{this.props.title}</p>
				<div style={{background: border_background, height: 2, margin: '0px 80px'}} />
			</div>
		return leftDiv
	}
}

class Home extends Component {
	static exercises = [
		{
			title: localStr('Identification'),
			type: 'Identification',
			items: ['note', 'interval', 'chord', 'scale', 'terms', 'harmony'],
		},
		{
			title: localStr('Construction'),
			type: 'Construction',
			items: ['note', 'interval', 'chord', 'scale'],
		},
		{
			title: localStr('Keyboard'),
			type: 'Keyboard',
			items: ['note', 'interval', 'chord', 'scale', 'rhythm'],
		},
		{
			title: localStr('Ear Training'),
			type: 'EarTraining',
			items: ['note', 'interval', 'chord', 'scale', 'rhythm'],
		},
		{
			title: localStr('Toolbox'),
			type: 'Toolbox',
			items: ['editor'/*, 'exam'*/],
		},
	];
	static homeIcons = {
		note: '\ue6c6',
		interval: '\ue6c4',
		chord: '\ue6c2',
		keysignature: '\ue6c3',
		scale: '\ue6c5',
		rhythm: '\ue6a1',
		editor: '\ue6c3',
	};
	static titles = {
		note: localStr('Note'),
		scale: localStr('Scale'),
		chord: localStr('Chord'),
		keysignature: localStr('Key Signature'),
		interval: localStr('Interval'),
		terms: localStr('Music Terms'),
		rhythm: localStr('Rhythm Melody'),
		harmony: localStr('Harmony'),
		exam: localStr('General Exercises'),
		editor: localStr('Create music score'),
	};
	constructor(props) {
		super(props)
		window.curComponent = this
		this.state = window.stateHome ? window.stateHome : {
			row: 0,
			column: 0,
		}
	}
	setState(state) {
		super.setState(state)
		window.stateHome = state(this.state)
	}
	takeAction(action) {
		let newState
		switch (action.type) {
		case 'moveColumn':
			let column = this.state.column + action.offset
			let row = this.state.row
			if (column<0) {
				if (row<=0) {
					return
				}else{
					row -= 1
					let exercise = Home.exercises[row]
					column = exercise.items.length-1
				}
			}else{
				let exercise = Home.exercises[row]
				if (column>=exercise.items.length) {
					if (row>=Home.exercises.length-1) {
						return
					}else {
						row += 1
						column = 0
					}
				}
			}
			newState = {row: row, column: column}
			break
		case 'moveRow':
		{
			let row = this.state.row + action.offset
			if (row<0 || row>=Home.exercises.length) {
				return
			}
			let column = this.state.column
			let exercise = Home.exercises[row]
			if (column>=exercise.items.length) {
				column = exercise.items.length-1
			}
			newState = {row: row, column: column}
		}
			break
		case 'OK':
		{
			let exercise = Home.exercises[this.state.row]
			let item = exercise.items[this.state.column]
			// window.location.href = this.linkOfExcise(exercise, item)
			window.theApp.pushWindow(this.linkOfExcise(exercise, item))
			return
		}
		default:
			break
		}
		this.setState(prevState => (newState))
	}
	onkeydown(e) {
		let SELF = window.curComponent
		let key = e.key
		if (key === 'ArrowUp') {
			SELF.takeAction({type: 'moveRow', offset: -1})
		}else if (key === 'ArrowDown') {
			SELF.takeAction({type: 'moveRow', offset: 1})
		}else if (key === 'ArrowLeft') {
			SELF.takeAction({type: 'moveColumn', offset: -1})
		}else if (key === 'ArrowRight') {
			SELF.takeAction({type: 'moveColumn', offset: 1})
		}else if (key === 'Enter') {
			SELF.takeAction({type: 'OK'})
		}else {
			return true
		}

		return false
	}
	linkOfExcise(exercise, item) {
		// if (exercise.type === 'Toolbox' && item === 'editor') {
		// 	return <MusicEditor />
		// }else{
			return <MusicSetting type={item} qtype={exercise.type}/>
		// }
	}
	render() {
		let width =  window.document.body.offsetWidth/2
		let cellWidth = window.device==='piano' ? width/8 : window.device==='mobile' ? 120 : 170
		let cellHeight = window.device==='piano' ? cellWidth*0.382 : window.device==='mobile' ? 50 : 60
		let elems = []
		for (var i = 0; i < Home.exercises.length; i++) {
			let exercise = Home.exercises[i]
			let values = exercise.items;
			let items = []
			for (let index=0; index<values.length; index++) {
			// let items = values.map((item, index) => {
				let item = values[index]
				let className = 'Home-icon'
				if(window.device !== 'mobile' && this.state.row===i && this.state.column===index) {
					className += ' Home-icon-hi'
				}
				let link = this.linkOfExcise(exercise, item)
				let iconStyle = {display: 'table-cell', fontSize:cellHeight/2, width: cellHeight/2+cellWidth/10, paddingLeft: cellWidth/10}
				if (window.device==='mobile') {
					iconStyle.lineHeight = '40px'
				}
				let icon = <div className='iconfont' style={iconStyle}>{Home.homeIcons[item]}</div>
				let title = Home.titles[item]
				items.push(
					<div key={item} onClick={(e) => {window.theApp.pushWindow(link)}} className={className} style={{width: cellWidth, height: cellHeight, marginLeft: cellWidth/11}}>
						{icon}
						<div style={{display: 'table-cell', width: cellWidth*0.6, height: cellHeight, lineHeight: 1,  verticalAlign: 'middle', textAlign: 'left'}}>
							{title}
						</div>
					</div>
				)
			}
			elems.push(
				<div key={i} style={{display: 'table', margin: cellHeight*0.35+'px 0px'}}>
					<li style={{fontSize: cellHeight*0.35}}>{exercise.title}</li>
					<div className='White-line' style={{width: cellWidth*4}}></div>
					{items}
				</div>
				)
		}
		let toolbar = null
		if (window.device!=='mobile') {
			toolbar = <Toolbar systemButtons={{arrow: true, ok: 'OK'}} action={(action) => this.takeAction(action)}/>
		}
		let leftDiv = null
		if (window.device === 'piano') {
			leftDiv = <LeftTitle title={localStr('Find Exercises on Theory of Music')} />
		}
		
		return (
			<div style={{padding: 10, display: 'table-cell'}}>
				{leftDiv}
				<div style={{display: 'table-cell'}}>{elems}</div>
				{toolbar}
			</div>)
	}
}

export default Home;



